.board {
    text-align: center;
}

.board span {
    display: inline-block;
}

.inputText {
    width: 40px;
    height: 40px;
}

.constInput {
    font-family: 'Montserrat', sans-serif;
    width: 380px;
    font-size: 20px;
    background-color: whitesmoke;
    border: 0;
    margin-top: 35px;
    outline: 0 !important;
    text-align: center;
}

.comp {
    background-color: lightgray;
    text-align: center;
    width: 45px;
    height: 45px;
    border: none;
    box-sizing: border-box;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
}

.constMainBox {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: 30px 0;
}

.constFieldBox {
    width: 60%;
}

.constStatusBtnsBox {
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    margin-right: 30px;
}

.constStatusBtn {
    display: flex;
    width: 250px;
    height: 50px;
    border-radius: 5px;
    border: none;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
    justify-content: center;
    align-items: center;
    font-size: 20px;
    outline: 0 !important;
    margin-top: 10px;
    background-color: lightgray;
}

.constStatusBtn:hover {
    cursor: pointer;
    font-weight: bold;
}

.constBottomBtnsBox {
    width: 550px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: auto;
}

.comp:hover {
    cursor: pointer;
}

